import { Box, Container, Typography } from '@material-ui/core';
import React from 'react';
import { useQuery } from 'react-query';
import { useParams } from 'react-router';
import searchProduct from '../services/products/searchProducts';
import Loading from '../components/loading';
import ProductCard from '../components/productCard';

const Search = () => {
  const { key } = useParams();

  const { data, isLoading } = useQuery('search', () => searchProduct(key));

  const showSearchResult = () => {
    if (data.length < 1) {
      return (
        <Typography style={{ marginTop: '2rem' }} align='center' variant='h5'>
          Sorry could not find the product you searched
        </Typography>
      );
    }

    return data.map((product) => (
      <ProductCard
        name={product.product_name}
        subtitle={product.brand && product.brand.brand_name}
        price={product.price}
        image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
        brandName={product.brand && product.brand.brand_name}
        bestSeller={product.best_seller}
        id={product.id}
        key={product.id}
        discount={product.discount_amount}
      />
    ));
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div style={{ minHeight: '60vh' }}>
      <Container>
        <Box margin='2rem 0'>
          <Typography align='center' variant='h4'>
            Search Results
          </Typography>
          <Box display='flex' flexWrap='wrap' justifyContent='space-between'>
            {showSearchResult()}
          </Box>
        </Box>
      </Container>
    </div>
  );
};

export default Search;
